'use client'
import { useEffect, useState } from 'react'

export default function ProjectDetail({ slug }: { slug: string }) {
  const [project, setProject] = useState<any | null>(null)

  useEffect(() => {
    fetch('/api/projects')
      .then((r) => r.json())
      .then((data) => setProject(data.find((p: any) => p.Slug === slug)))
  }, [slug])

  if (!project) return <div className="p-10 text-center">Loading...</div>

  return (
    <main className="container mx-auto px-4 py-10">
      <img src={`/img/${project.Banner || project.Image}`} alt="" className="w-full h-72 object-cover rounded mb-8" />
      <h1 className="text-3xl font-bold mb-4">{project.Title}</h1>
      <p className="text-slate-700 mb-6">{project.Description || project.ShortDescription}</p>
      <h3 className="font-semibold mb-2">Services Provided</h3>
      <ul className="list-disc pl-6 text-slate-700 mb-6">
        {(project.Services || '').split(',').map((s, i) => (
          <li key={i}>{s.trim()}</li>
        ))}
      </ul>
      <a href="/contact" className="inline-block bg-blue-600 text-white px-6 py-2 rounded">Contact Us</a>
    </main>
  )
}
